@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body, html {
  background: #de3d3b;
  /* fallback for old browsers */
  background: -webkit-linear-gradient(to right, #1C3052, #de3d3b);
  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #1C3052, #de3d3b);
  /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: white;
  overflow-x: hidden;
  font-family: 'Poppins', sans-serif;
  letter-spacing: 0.03em;
  line-height: 1.6195;
}

.Head {
  background-color: #ffff;
  color: black;
  text-align: center;
  margin: 50px 0px 50px 0px;
}

.Head h1 {
  font-size: calc(20pt + 0.15vw);
  padding: 15px 0px 15px 0px;
}

.Head h1 #Animated__Bg {
  position: relative;
  padding: 0px 10px 0px 10px;
}

.Head h1 #Animated__Bg::after {
  content: "";
  width: 100%;
  position: absolute;
  bottom: 0;
  left: 0;
  height: 40%;
  opacity: .56;
  background-color: #423F8D;
  transition: .3s ease-in-out;
  -webkit-transition: .3s ease-in-out;
  -moz-transition: .3s ease-in-out;
  -ms-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
}

.Head h1 #Animated__Bg:hover::after {
  content: "";
  width: 100%;
  position: absolute;
  bottom: 0;
  cursor: pointer;
  left: 0;
  height: 100%;
  opacity: .56;
  transition: .5s ease-in-out;
  background-color: #423F8D;
  -webkit-transition: .5s ease-in-out;
  -moz-transition: .5s ease-in-out;
  -ms-transition: .5s ease-in-out;
  -o-transition: .5s ease-in-out;
}

.Center_Div {
  display: flex;
  flex-direction: column;
  text-align: center;
  height: 50vh;
}

.Child__Center {
  border-left: 3px solid white;
  border-right: 6px solid #de3d3b;
  border-top: 3px solid white;
  border-bottom: 6px solid #de3d3b;
  border-radius: 20px;
  padding: 50px 30px 50px 30px;
  width: fit-content;
  margin: auto;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
}

.Child__Center h1 {
  padding: 20px 0p 20px 0px;
  text-shadow: 2px 2px 2px #de3d3b;
}

.Btn {
  border-radius: 50%;
  -webkit-border-radius: 50%;
  -moz-border-radius: 50%;
  -ms-border-radius: 50%;
  -o-border-radius: 50%;
  width: 50px;
  cursor: pointer;
  height: 50px;
  border: none;
  margin: 30px 20px 10px 20px;
  font-size: 30px;
  box-shadow: #de3d3b 1.95px 1.95px 2.6px;
  color: #1C3052;
  font-weight: bolder;
  transition: .3s ease-in-out;
  -webkit-transition: .3s ease-in-out;
  -moz-transition: .3s ease-in-out;
  -ms-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
}

#Error {
  padding: 20px 0px 0px 0px;
  color: #de3d3b !important;
  transition: .5s ease-in-out;
  -webkit-transition: opacity 100ms;
  -moz-transition: opacity 100ms;
  -ms-transition: opacity 100ms;
  -o-transition: opacity 100ms;
  opacity: 0;
  transition: opacity 100ms;
}

#Error span {
  background-color: white;
  padding: 5px;
  border-bottom: 5px solid;
  border-right: 5px solid;
  border-top-left-radius: 10px;
  font-size: calc(8pt + 0.15vw);
  letter-spacing: 0.03em;
  box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.Btn:hover {
  background-color: #de3d3b;
  transition: .3s ease-in-out;
  -webkit-transition: .3s ease-in-out;
  -moz-transition: .3s ease-in-out;
  -ms-transition: .3s ease-in-out;
  -o-transition: .3s ease-in-out;
  color: white;
}

@media screen and (max-width: 768px) {
  .Head h1 {
    font-size: calc(16pt + 0.15vw);
  }
}

@media screen and (max-width: 640px) {
  .Head h1 {
    font-size: calc(14pt + 0.15vw);
  }
}

@media screen and (max-width: 320px) {
  .Head h1 {
    font-size: calc(12pt + 0.15vw);
  }
}